/** content.less **/
.content {
  width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.content__nav {
  display: flex;
  justify-content: center;
}
.content__nav .tablinks {
  background: none;
  border: none;
  outline: none;
  margin: 40px 12px 16px 12px;
  padding: 0;
}
.content__nav a.active {
  color: #E5261E;
  border-bottom: 2px solid #E5261E;
}
.content__nav h1 {
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  font-family: "Rubik", "Arial", sans-serif;
  margin: 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .content {
    width: 668px;
  }
}
@media (min-width: 1150px) {
  .content {
    width: 1180px;
  }
}
/** footer.less **/
footer {
  background: #F2F2F2;
  margin-top: 72px;
}
footer .footer {
