}
/** genres.less **/
.genres h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 28px;
  margin: 16px 0;
}
.genres__items {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}
.genres__items .item {
  width: 280px;
  height: 208px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 16px;
}
.genres__items .item-1 {
  background: linear-gradient(136.27deg, rgba(242, 201, 76, 0.8) 8.44%, rgba(242, 154, 74, 0.8) 91.36%);
  box-shadow: inset 0 0 16px rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
}
.genres__items .item-1:hover {
  background: linear-gradient(136.27deg, #f2994a 8.44%, #f29a4a 91.36%);
}
.genres__items .item-2 {
  background: linear-gradient(136.27deg, rgba(242, 153, 74, 0.8) 8.44%, rgba(235, 87, 87, 0.8) 91.36%);
  box-shadow: inset 0 0 16px rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
}
.genres__items .item-2:hover {
  background: linear-gradient(136.27deg, #f2994a 8.44%, #eb5757 91.36%);
}
.genres__items .item-3 {
  background: linear-gradient(136.27deg, rgba(86, 204, 242, 0.8) 8.44%, rgba(47, 128, 237, 0.8) 91.36%);
  box-shadow: inset 0 0 16px rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
}
.genres__items .item-3:hover {
  background: linear-gradient(136.27deg, #56ccf2 8.44%, #2f80ed 91.36%);
}
.genres__items .item-4 {
  background: linear-gradient(136.27deg, rgba(130, 130, 130, 0.8) 8.44%, rgba(51, 51, 51, 0.8) 91.36%);
  box-shadow: inset 0 0 16px rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
}
.genres__items .item-4:hover {
  background: linear-gradient(136.27deg, #828282 8.44%, #333333 91.36%);
}
.genres__items img {
  border-radius: 8px;
  margin-top: 80px;
}
.genres__items h4 {
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  color: #FFFFFF;
  margin: 0;
  padding-top: 28px;
}
@media (min-width: 768px) {
  .genres__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .genres__items .item {
    margin-bottom: 16px;
  }
}
@media (min-width: 1150px) {
  .genres__items {
    justify-content: space-between;
  }
  .genres__items .item {
    margin-bottom: 0;
  }
}
