/** login.less **/
.login {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(51, 51, 51, 0.6);
}
.login__form {
  position: absolute;
  width: 304px;
  height: 394px;
  background: #FFFFFF;
  border-radius: 8px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login__form h2 {
  margin: 32px 0 24px 0;
  font-size: 28px;
  font-weight: 600;
  line-height: 33px;
}
.login__form fieldset {
  width: 232px;
  padding: 0;
  border: none;
}
.login__form fieldset input {
  width: 232px;
  outline: none;
  border: none;
  border-bottom: 1px solid #333333;
  height: 25px;
  margin-bottom: 24px;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  font-family: "Rubik", "Arial", sans-serif;
}
.login__form fieldset input::placeholder {
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: #828282;
  padding-bottom: 9px;
  font-family: "Rubik", "Arial", sans-serif;
}
.login__form .container {
  display: block;
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  user-select: none;
  margin-right: 124px;
}
.login__form .container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.login__form .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 16px;
  border: 1px solid #333333;
  box-sizing: border-box;
  border-radius: 4px;
}
.login__form .checkmark::after {
  content: "";
  position: absolute;
  display: none;
}
.login__form .container input:checked ~ .checkmark::after {
  display: block;
}
.login__form .container .checkmark::after {
  left: 4.5px;
  top: 1px;
  width: 4px;
  height: 9px;
  border: solid #E5261E;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.login__form .log-btn {
  width: 113px;
  min-height: 38px;
  background: #E5261E;
  border-radius: 4px;
  outline: none;
  border: none;
  color: #FFFFFF;
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  cursor: pointer;
  margin-top: 112px;
}
body {
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  margin: 0;
}
a {
  text-decoration: none;
  color: #333333;
}
/** news.less **/
.news h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 28px;
  margin: 16px 0;
}
.news h2 img {
  margin-right: 10px;
}
.news__items {
  display: flex;
  flex-direction: column;
}
.news__items .item {
  width: 280px;
}
.news__items .item__img {
  position: relative;
  height: 370px;
}
.news__items .item__img:hover .item__hover {
  display: block;
}
.news__items .item__hover {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background: #333333;
  opacity: 0.8;
  border-radius: 8px;
  padding: 16px;
  display: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #FFFFFF;
}
.news__items img {
  border-radius: 8px;
}
.news__items h3 {
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  margin: 16px 0;
}
@media (min-width: 768px) {
  .news__items {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: normal;
    flex-wrap: wrap;
  }
}
@media (min-width: 1150px) {
  .news__items {
    justify-content: space-between;
    flex-direction: row;
  }
}
/** tv-program.less **/
.tv {
  width: 340px;
  height: 696px;
  margin-top: 15px;
}
.tv .tv-item {
  background: #F2F2F2;
  border-radius: 8px;
  height: auto;
  margin: 16px 0 0 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  justify-content: space-around;
}
.tv .tv-item__channel {
  display: flex;
  justify-content: center;
}
.tv .tv-item__program h2 {
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  margin: 21px 0 17px 0;
}
.tv .tv-item__program .active {
  color: #E5261E;
}
.tv .tv-item__program p {
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  margin: 12px 0 0 0;
  width: 280px;
}
.tv .tv-item__program p span {
  margin-right: 22px;
}
.tv .tv-item__program p:last-child {
  margin-bottom: 19px;
}
.tv .tv-item:first-child {
  margin-top: 0;
}
@media (min-width: 768px) {
