  }
  .tv .tv-item {
    flex-direction: row;
    justify-content: initial;
    padding: 0;
  }
  .tv .tv-item__channel {
    width: 150px;
  }
  .tv .tv-item__program p {
    width: 518px;
    margin: 12px 0;
  }
}
@media (min-width: 1150px) {
  .tv {
    width: 1200px;
  }
  .tv .tv-item__channel {
    width: 400px;
  }
  .tv .tv-item__program p {
