  }
}
.user {
  display: flex;
  align-items: center;
  height: 23px;
  justify-content: flex-end;
}
.user p {
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  padding-right: 10px;
  width: max-content;
  cursor: pointer;
}
.user p:hover {
  opacity: 0.8;
}
.user__changename {
  display: flex;
}
.user__changename .change-name {
  width: max-content;
  height: 20px;
  display: none;
  border: none;
  border-bottom: 1px solid;
  outline: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  font-family: "Rubik", "Arial", sans-serif;
  margin-right: 10px;
}
.user__changename .change-name::-ms-clear {
  display: none;
}
.user__changename .name-save {
  display: none;
  background: none;
  margin: 5px 10px 0 -25px;
  height: 9px;
  width: 15px;
  outline: none;
  cursor: pointer;
  border: solid #E5261E;
  border-width: 0 0 2px 2px;
  transform: rotate(315deg);
}
.user__logout {
  display: none;
  background: none;
  outline: none;
  border: none;
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  color: #E5261E;
  cursor: pointer;
}
.user__login-btn {
  width: 113px;
  min-height: 38px;
  background: #E5261E;
  border-radius: 4px;
  outline: none;
  border: none;
  color: #FFFFFF;
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .user {
    width: 182px;
    height: auto;
  }
  .user__changename .change-name {
    width: 130px;
  }
}
@media (min-width: 1150px) {
  .user__changename .change-name {
    width: max-content;
  }
  .user__changename:hover .name-save {
    display: block;
  }
}
